<template>
  <div class="my-head-index">
      <!--  -->
      <div @click="routeTo('FM')">FM</div>
      <div @click="routeTo('songsList')"></div>
      <div @click="routeTo('rankingList')"></div>
      <div @click="routeTo('singer')"></div>
  </div>
</template>

<script>
export default {
  name: 'homeindex',
  methods: {
    routeTo (aim) {
      this.$router.push(`/${ aim }`)
    },
  }
}
</script>

<style scoped>
  .my-head-index {
    display: flex;
    justify-content: space-around;
    align-items: center;
    border-bottom: 1px solid rgb(23, 213, 238);
  }
  .my-head-index div {
    /* width: 50px; */
    /* height: 50px; */
    color: beige;
    padding: 5px 10px;
    margin: 5px 0;
    border-radius: 4px;
    background: rgba(0, 0, 0, .5);
  }
</style>





